/**button*/
.wrapper {
  margin-top: 28px;
  position: relative;
  top: 60%;
  left: 50%;
  letter-spacing: 2px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: -moz-fit-content;
  width: -webkit-fill-available;
  height: auto;
}
button {
  width: -webkit-fill-available;
  height: 50px;
  background: linear-gradient(to left top, #fb2d2d 50%, #ff3838 50%);
  border-style: none;
  color: #fff;
  font-size: 23px;
  letter-spacing: 3px;
  /* font-family: 'Lato'; */
  font-weight: 600;
  outline: none;
  cursor: pointer;
  position: relative;
  padding: 0px;
  overflow: hidden;
  transition: all .5s;
  box-shadow: 0px 1px 2px rgba(0,0,0,.2);
}
button span{
    position: absolute;
    display: block;
}
button span:nth-child(1){
    height: 3px;
    width:200px;
    top:0px;
    left:-200px;
    background: linear-gradient(to right, rgba(0,0,0,0), #f6e58d);
    border-top-right-radius: 1px;
    border-bottom-right-radius: 1px;
    -webkit-animation: span1 2s linear infinite;
            animation: span1 2s linear infinite;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

@-webkit-keyframes span1{
    0%{
        left:-200px
    }
    100%{
        left:200px;
    }
}

@keyframes span1{
    0%{
        left:-200px
    }
    100%{
        left:200px;
    }
}
button span:nth-child(2){
    height: 70px;
    width: 3px;
    top:-70px;
    right:0px;
    background: linear-gradient(to bottom, rgba(0,0,0,0), #f6e58d);
    border-bottom-left-radius: 1px;
    border-bottom-right-radius: 1px;
    -webkit-animation: span2 2s linear infinite;
            animation: span2 2s linear infinite;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
}
@-webkit-keyframes span2{
    0%{
        top:-70px;
    }
    100%{
        top:70px;
    }
}
@keyframes span2{
    0%{
        top:-70px;
    }
    100%{
        top:70px;
    }
}
button span:nth-child(3){
    height:3px;
    width:200px;
    right:-200px;
    bottom: 0px;
    background: linear-gradient(to left, rgba(0,0,0,0), #f6e58d);
    border-top-left-radius: 1px;
    border-bottom-left-radius: 1px;
    -webkit-animation: span3 2s linear infinite;
            animation: span3 2s linear infinite;
    -webkit-animation-delay: 3s;
            animation-delay: 3s;
}
@-webkit-keyframes span3{
    0%{
        right:-200px;
    }
    100%{
        right: 200px;
    }
}
@keyframes span3{
    0%{
        right:-200px;
    }
    100%{
        right: 200px;
    }
}

button span:nth-child(4){
    height:70px;
    width:3px;
    bottom:-70px;
    left:0px;
    background: linear-gradient(to top, rgba(0,0,0,0), #f6e58d);
    border-top-right-radius: 1px;
    border-top-left-radius: 1px;
    -webkit-animation: span4 2s linear infinite;
            animation: span4 2s linear infinite;
    -webkit-animation-delay: 4s;
            animation-delay: 4s;
}
@-webkit-keyframes span4{
    0%{
        bottom: -70px;
    }
    100%{
        bottom:70px;
    }
}
@keyframes span4{
    0%{
        bottom: -70px;
    }
    100%{
        bottom:70px;
    }
}

