.hamburger {
    display: none;
}

.bar {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
    background-color: #101010;
}


.nav-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-item {
    margin-left: 2rem;
}

.nav-link{
    font-size: 1.2rem;
    font-weight: 400;
    color: #475569;
    color: black;
     padding: 10px;
  border: 0.1px rgba(156, 156, 156, 0.2);
  border-radius: 20px;

}

.nav-link:hover{
    color: #482ff7;
}


.nav-profile{
    color: black;
  display:inline-flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 10px;
  border: 0.1px  rgba(156, 156, 156, 0.2);
  border-radius: 20px;
  border-style: solid;
  font-family:  Papyrus,Brush Script MT,Lucida Handwriting;
font-family: 'Pacifico', cursive;  
  font-weight: bold;
  font-size: 1.5rem;

}
.nav-profile:hover,.nav-link:hover{
  color: black;
  border-radius: 20px;
   padding: 8px 10px;
  cursor: pointer;
  box-shadow: 0.1px 0.1px 10px 0.1px grey;
}

.nav-logo {
    font-size: 2.0rem;
    font-weight: 500;
    color: #482ff7;
}



@media only screen and (max-width: 768px) {
    .nav-menu {
        position: fixed;
        left: -100%;
        top: 5rem;
        flex-direction: column;
        background-color: #fff;
        width: 100%;
        border-radius: 10px;
        text-align: center;
        transition: 0.3s;
        box-shadow:
            0 10px 27px rgba(0, 0, 0, 0.05);
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 2.5rem 0;
    }
    .nav-link{
        border: 0px;
        padding: 10px 50px;
    }
    .nav-profile{
        align-self: center;
        justify-content: center;
        border: 0px;
    }
    .nav-profile:hover{
         padding: 8px 20px;
         box-shadow: none;
    }
    .hamburger {
        display: block;
        cursor: pointer;
    }
    .hamburger.active .bar:nth-child(2) {
        opacity: 0;
    }

    .hamburger.active .bar:nth-child(1) {
        -webkit-transform: translateY(8px) rotate(45deg);
                transform: translateY(8px) rotate(45deg);
    }

    .hamburger.active .bar:nth-child(3) {
        -webkit-transform: translateY(-8px) rotate(-45deg);
                transform: translateY(-8px) rotate(-45deg);
    }
}
